<template>
  <div>
    <h2>Без Vuex</h2>
    <form action="javascript:void(0)" @submit="post">
      <input
        type="number"
        required
        v-model="price"
        @input="changed('price')"
      />
      <input
        type="number"
        required
        v-model="qty"
        @input="changed('qty')"
      />
      <input
        type="number"
        required
        v-model="amount"
        @input="changed('amount')"
      />
      <button type="submit">button 4</button>
      <div>price: {{ labels.price }}</div>
      <div>qty: {{ labels.qty }}</div>
      <div>amount: {{ labels.amount }}</div>
      <pre>localStorage: {{ JSON.stringify($parent.localStorage, null, "  ") }}</pre>
    </form>
    <h3>Сообщения:</h3>
    <div>
      <pre
        v-for="(message, index) in messages"
        :key="index"
      >{{ message }}</pre>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TestData',
  props: {},
  data() {
    return {
      nonce: 0,
      price: 0,
      qty: 0,
      amount: 0,
      labels: {
        price: 0,
        qty: 0,
        amount: 0,
      },
      order: [], // Порядок изменений, массив имён переменных;
      messages: [],
    };
  },
  watch: {
    price() {
      setTimeout(() => this.labels.price = this.price, 300);
    },
    qty() {
      setTimeout(() => this.labels.qty = this.qty, 300);
    },
    amount() {
      setTimeout(() => this.labels.amount = this.amount, 300);
    },
  },
  methods: {
    async post() {
      const data = {
        price: this.price,
        qty: this.qty,
        amount: this.amount,
        nonce: this.nonce,
      };
      let promise = new Promise((resolve, reject) => {
        setTimeout(() => {
          if (data.amount % 2 === 0) {
            resolve({success: true});
          } else {
            reject({success: false});
          }
        }, 1000);
      });
      this.messages.unshift(
        `Отправлено: ${JSON.stringify(data, null, "  ")}\n` +
        `Сейчас в localStorage: ${JSON.stringify(this.$parent.localStorage, null, "  ")}`
      );
      promise
        .then(result => {
          this.$parent.toLocalStorage(data);
          this.messages.unshift(
            `Ответ: ${JSON.stringify(result, null, "  ")}\n` +
            `Сейчас в localStorage: ${JSON.stringify(this.$parent.localStorage, null, "  ")}`
          );
        })
        .catch(error => {
          this.messages.unshift(
            `Ответ: ${JSON.stringify(error, null, "  ")}\n` +
            `Сейчас в localStorage: ${JSON.stringify(this.$parent.localStorage, null, "  ")}`
          );
        })
      ;
    },
    changed(varName) {
      let message = '';
      const varIndex = this.order.indexOf(varName);
      if (varIndex !== -1) this.order.splice(varIndex, 1);
      switch (this.order[0]) {
        case 'price':
          if (this.qty == 0) {
            message = 'Попытка вычислить “price”, поделив “amount” на нулевую “qty”.';
            break;
          }
          this.price = this.amount / this.qty;
          message = 'Пересчиталась “price”.';
          break; 
        case 'qty':
          if (this.price == 0) {
            message = 'Попытка вычислить “qty”, поделив “amount” на нулевую “price”.';
            break;
          }
          this.qty = this.amount / this.price;
          message = 'Пересчиталась “qty”.';
          break; 
        case 'amount':
          this.amount = this.price * this.qty;
          message = 'Пересчиталась “amount”.';
          break; 
      }
      this.order.push(varName);
      message = `Раньше других менялась “${this.order[0]}”. ${message}`;
      this.messages.unshift(message);
      this.nonce++;
    },
  },
};
</script>

<style scoped>
form {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr min-content;
  gap: 10px;
}
pre:not(form pre) {
  padding: 0.5rem;
  outline: 1px solid rgba(0, 0, 0, 0.2);
}
</style>
