<template>
  <div id="app">
    <TestData />
    <hr />
    <TestVuex />
  </div>
</template>

<script>
import TestData from './components/TestData.vue'
import TestVuex from './components/TestVuex.vue'

export default {
  name: 'App',
  components: {
    TestData,
    TestVuex,
  },
  data() {
    return {
      localStorage: {
        nonce: window.localStorage.getItem('nonce'),
        price: window.localStorage.getItem('price'),
        qty: window.localStorage.getItem('qty'),
        amount: window.localStorage.getItem('amount'),
      },
    };
  },
  watch: {
    localStorage: {
      deep: true,
      immediate: true,
      handler(ls) {
        for (let key in ls) {
          window.localStorage.setItem(key, ls[key])
        }
      },
    },
  },
  methods: {
    toLocalStorage(data) {
      this.localStorage = data;
    },
  },
};
</script>
